// deco border
.hover-decoBorder-bottom {
  &::after {
    content: '';
    position: absolute;
    width: 0%;
    height: 3px;
    background: transparent;
    bottom: 0;
    left: 0;
    transition: all 0.3s;
  }

  &:hover {
    &::after {
      content: '';
      position: absolute;
      width: 100%;
      height: 5px;
      background: $primary;
      bottom: 0;
      left: 0;
    }
  }
}

.hover-opacity {
  &:hover {
    opacity: 0.7;
  }
}

.hover-background-secondary {
  &:hover {
    background: $secondary;
  }
}
